import './App.css';
import Home from './pages/home.js';
import EtrainingHome from './pages/etrainingHome.js'
import ModuleOneHome from './pages/moduleone-home.js'
import data from './pages/modules.json'
import ModuleOneOne from './pages/moduleone-one.js'

import { BrowserRouter as Router, Route, Routes, } from "react-router-dom";
const links = data.map((data) => {
  const link = "/kerjakudev2//moduleone" + data.id

  return (
    <Route path={link} element={<ModuleOneHome />} />
  )
}
)
function App() {
  return (
    <Router>
      <Routes>
        <Route path="/kerjakudev2" element={<Home />} />
        <Route path="/kerjakudev2/etrainingHome" element={<EtrainingHome />} />
        <Route path="/kerjakudev2/moduleoneHome" element={<ModuleOneHome />} />
        {links}
        <Route path="/moduleoneOne" element={<ModuleOneOne />} />
      </Routes>
    </Router>
  );
}

export default App;
